

:root {
    --boxBackground: rgb(255,255,255,.5);
    --boxBorder: #F87575;
	--clear: rgb(255,255,255,0)
}
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;

}

html{
	overflow: hidden;
}

body{
	background:url(/ada-project-docs/assets/broken_clouds_sky.jpg) no-repeat top fixed;
	background-size: 100% 70%;
	height: 100vh;
	width: 100vw;
	min-height: 100vh;
	margin: 0;
	padding: 0;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-content: center;
	justify-items: center;
}

footer img{
	position: absolute;
	bottom: 0;
	z-index: -1;
	width: 100%;
	max-width: 100%;
}


.main-box {
	margin: auto;
	width: 36%;
	height: auto;
	align-self: center;
	min-width: 300px;
	background-color: var(--boxBackground);
	border-radius: 3px;
	border: 2px solid #009688;
	box-shadow: 0 8px 10px 1px rgba(0,0,0,.14), 0 3px 14px 2px rgba(0,0,0,.12), 0 5px 5px -3px rgba(0,0,0,.2);
  }

.location-box{
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-content: center;
	margin-top: 2%;
}

.dropdown-location{
	align-self: center;
}

#location-dropdown{
	min-width: 170px;
}



#current-location-container, #selected-location-container{
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-content: center;
	width: 100%;
	height: 100%;
}
input{
	border-radius: 0 !important;
}

#current-location-container{
	border-top: lightgray solid 1px;
}

#current-location, #selected-location{
	border: none;
	background-color: white;
	width: 100%;
	height: 100%;
	padding-top: 3%;
	padding-bottom: 3%;
}


#current-location:hover, #selected-location:hover{
	background-color: #009688;
}

.dropdown-menu{
	padding: 0px;
	margin: 0px
}


.bi-globe{
	font-size: 2em;
}

.temperature-box{
	margin-top: 5%;
	margin-bottom: 5%;
}
#temp {
	font-size: 10em;
}

.toptempbox{
	display: flex;
	flex-direction: row;
	justify-content: center;
}

.tempbox{
	align-self: center;
}

.change-temp-buttons{
	margin-right: 3%;
}

.change-temp-buttons, .change-sky-buttons{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-content: center;
}

.temp-button, .sky-button{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-content: center;
	height: 10%;
	width: 100%;
	background-color: var(--clear);
}


.custom-switch{
	display: flex;
	flex-direction: row;
	justify-content: center;
}

.toggle.ios, .toggle-on.ios, .toggle-off.ios { 
	border-radius: 20rem; 
}

.toggle.ios .toggle-handle { 
	border-radius: 20rem; 
}

.wconditionsbox{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-content: center;
	min-width: 8vw;
}


.weather-icon{
	font-size: 2em;
	margin: 0;
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	padding-left: 5%;
}


#wdescription{
	font-size: 1em;
	margin: 0;
	min-width: 100%;
	text-align: center;
	margin-top: 5%;
	flex-wrap: wrap;
	background-color:var(--clear);
}

#weather-dropdown-menu{
	min-width:100% !important;
	float:none !important;
	text-align: center !important;
	/* padding-left:0%;
	padding-right:12%; */
	padding-top: 5%;
	padding-bottom: 5%;
}

.weather-dropdown-item{
	background-color: white;
	border: none;
	height: 100%;
	padding-top: 2%;
	padding-bottom: 2%;
	width: 100%;
}

.weather-dropdown-item:hover{
	background-color: #009688;
}


.show {display:block;}